.nav {
  &__list {
    display: flex;
  }

  &__link {
    font-family: var(--font-family);
    font-weight: 400;
    font-style: normal;
    font-size: 12px;
    line-height: 33px;
    text-transform: uppercase;
    color: var(--text-main);
  }

  &__item {
    &:not(:last-child) {
      margin-right: 30px;
    }
  }
}