.hero {
  margin-bottom: 50px;
  background-image: url("../img/main-image.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 186px 310px 24px;
    color: #fff;
  }

  &__title {
    margin: 0;
    padding: 0 110px 24px;
  }

  &__sub-title {
    margin: 0;
    padding-bottom: 24px;
    font-weight: 400;
    font-size: 24px;
    line-height: 40px;
    text-align: center;
  }

  .hero &__btn {
    margin-bottom: 24px;
    padding: 13px 29px 14px;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    text-align: center;
    color: #0024ff;

    &_white.btn {
      background-color: #fff;
    }
  }

  &__options {
    display: flex;
    padding-bottom: 220px;
  }
}

.options {
  display: flex;
  justify-content: space-around;
  width: 100%;

  &__item {
    font-family: "Arial", sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 31px;
    color: #f6f6f8;
  }
}