:root {
  --font-family: "Inter", sans-serif;
  --second-family: "Arial", sans-serif;
  --third-family: "Verdana", sans-serif;
  --content-width: 1440px;
  --container-offset: 36px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --text-main: #000;
  --main-color: #0024ff;
}

