html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  color: var(--text-main);
  background-color: #f6f7f8;
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.overlay {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
}
.overlay_visible {
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease-in-out;
}

.main-title {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 44px;
  line-height: 50px;
  text-align: center;
  color: #fff;
}

.header {
  padding-top: 18px;
  padding-bottom: 10px;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__logo {
  flex-shrink: 1;
  min-width: 86px;
}
.header__nav {
  display: flex;
  flex-grow: 1;
  justify-content: center;
}
.header__button {
  flex-shrink: 1;
  min-width: 163px;
}

.hero {
  margin-bottom: 50px;
  background-image: url("../img/main-image.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 186px 310px 24px;
  color: #fff;
}
.hero__title {
  margin: 0;
  padding: 0 110px 24px;
}
.hero__sub-title {
  margin: 0;
  padding-bottom: 24px;
  font-weight: 400;
  font-size: 24px;
  line-height: 40px;
  text-align: center;
}
.hero .hero__btn {
  margin-bottom: 24px;
  padding: 13px 29px 14px;
  font-weight: 400;
  font-size: 18px;
  line-height: 28px;
  text-align: center;
  color: #0024ff;
}
.hero .hero__btn_white.btn {
  background-color: #fff;
}
.hero__options {
  display: flex;
  padding-bottom: 220px;
}

.options {
  display: flex;
  justify-content: space-around;
  width: 100%;
}
.options__item {
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 31px;
  color: #f6f6f8;
}

.activities {
  margin-bottom: 80px;
}
.activities__list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 36px;
}
.activities__title {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 42px;
  line-height: 49px;
  color: #161824;
}
.activities__card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-column: 4 span;
  height: 100%;
}

.testimonials {
  margin-bottom: 80px;
}
.testimonials__list {
  display: flex;
  justify-content: space-between;
}
.testimonials__icon {
  align-self: flex-end;
}
.testimonials__title {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 42px;
  line-height: 49px;
  color: #161824;
}
.testimonials__item {
  flex-basis: 432px;
}
.testimonials .card {
  padding: 30px 42px 122px 30px;
}

.services__container {
  background-color: #fff;
}
.services__list {
  display: flex;
  justify-content: space-between;
  padding: 20px 182px;
  gap: 100px;
}
.services__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.services__link {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 49px;
  color: #484848;
  background-color: transparent;
}
.services__link:hover {
  background-color: var(--main-color);
}
.services__link:hover .services__svg {
  background-color: var(--main-color);
}
.services__link:hover .services__svg path {
  fill: var(--light-color);
}
.services__svg {
  margin-bottom: 10px;
}
.services__svg path {
  fill: #0024ff;
}

.nav__list {
  display: flex;
}
.nav__link {
  font-family: var(--font-family);
  font-weight: 400;
  font-style: normal;
  font-size: 12px;
  line-height: 33px;
  text-transform: uppercase;
  color: var(--text-main);
}
.nav__item:not(:last-child) {
  margin-right: 30px;
}

.btn {
  padding: 6px 10px;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 12px;
  line-height: 28px;
  white-space: nowrap;
  text-align: center;
  background-color: var(--main-color);
  cursor: pointer;
  color: var(--light-color);
}

.card {
  padding: 65px 44px 65px 30px;
  background-color: #fff;
}
.card__title {
  position: relative;
  margin: 0;
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 24px;
  line-height: 32px;
  color: #161824;
}
.card__descr {
  flex-grow: 1;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  color: #161824;
}
.card__link {
  margin-top: auto;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  color: #0024ff;
}
.card.testimonials__item {
  display: flex;
  flex-direction: column;
}
.card.testimonials__item:not(:last-child) {
  margin-right: 36px;
}