.services {
  &__container {
    background-color: #fff;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    padding: 20px 182px;
    gap: 100px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__link {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 49px;
    color: #484848;
    background-color: transparent;

    &:hover {
      background-color: var(--main-color);

      .services__svg {
        background-color: var(--main-color);
      }

      .services__svg path {
        fill: var(--light-color);
      }
    }
  }

  &__svg {
    margin-bottom: 10px;

    path {
      fill: #0024ff;
    }
  }
}