.testimonials {
  margin-bottom: 80px;

  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__icon {
    align-self: flex-end;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 42px;
    line-height: 49px;
    color: #161824;
  }

  &__item {
    flex-basis: 432px;
  }

  & .card {
    padding: 30px 42px 122px 30px;
  }
}