.header {
  padding-top: 18px;
  padding-bottom: 10px;

  &__container {
    @include flex-v-center;

    justify-content: center;
  }

  &__logo {
    flex-shrink: 1;
    min-width: 86px;
  }

  &__nav {
    display: flex;
    flex-grow: 1;
    justify-content: center;
  }

  &__button {
    flex-shrink: 1;
    min-width: 163px;
  }
}