.activities {
  margin-bottom: 80px;

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 36px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 42px;
    line-height: 49px;
    color: #161824;
  }

  &__card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-column: 4 span;
    height: 100%;
  }
}